.header {
  background: #34b2e7;
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

.logo {
  font-size: 24px;
}

.user {
  font-size: 16px;
  color: rgb(230, 225, 216);
  outline: 2px solid #6bd3ff;
  padding: 7px 14px;
  border-radius: 30px;
  font-weight: 600;
}

.user:hover {
  background-color: #6bd3ff;
  color: #333;
}

.list {
  list-style: none;
  display: flex;
  gap: 25px;
}

@media (max-width: 620px) {
  .list li:not(:first-child) {
    display: none;
    
  }

  .list li button{
    font-size: 14px;
  }
  
  .logo {
    font-size: 18px;
  }

  .user {
    font-size: 14px;
  }
}
